				"html": "<p>Using <a href=\"http://daringfireball.net/projects/markdown/\">markdown</a> is a quick way to set up a <em>semi-formatted</em> post or page. It can really bring a page to life quickly &#x26; help to visually define the layout of text. This site is built with <a href=\"http://jekyllrb.com\">jekyll</a> so I'm using <a href=\"http://kramdown.gettalong.org/\">kramdown</a>, a version of markdown.</p>\n<h2>Below is a useful cheatsheet, mainly for me.</h2>\n<pre><code class=\"language-html\">**Bold**\n</code></pre>\n<p><strong>Bold</strong></p>\n<pre><code class=\"language-html\">_Italics_\n</code></pre>\n<p><em>Italics</em></p>\n<pre><code class=\"language-html\">`Monospaced`\n</code></pre>\n<p><code>Monospaced</code></p>\n<pre><code class=\"language-html\">> Quoted text\n</code></pre>\n<blockquote>\n<p>Quoted text</p>\n</blockquote>\n<pre><code class=\"language-html\">[link](http://makandra.com/)\n</code></pre>\n<p><a href=\"http://makandra.com/\">link</a></p>\n<pre><code class=\"language-html\">![image alt text](//placekitten.com/g/900/600)\n</code></pre>\n<p><img src=\"//placekitten.com/g/900/600\" alt=\"image alt text\"></p>\n<p>If you want to enforce a line break,\\\nyou need to use two backslashes.</p>\n<pre><code class=\"language-html\"># Headline\n</code></pre>\n<h1>Headline</h1>\n<p>Lorem ipsum...</p>\n<p>Horizontal Rule Below</p>\n<hr>\n<p>Code</p>\n<p>wrap a code block with '`<code></code>'</p>\n<pre><code class=\"language-html\">def foo\n  \"hello!\"\nend\n</code></pre>\n<p>Unordered List</p>\n<pre><code class=\"language-html\">\\* Unordered List Item\n</code></pre>\n<ul>\n<li>Unordered List Item 1</li>\n<li>Unordered List Item 2</li>\n</ul>\n<p>Ordered List</p>\n<ol>\n<li>Ordered List Item 1</li>\n<li>Ordered List Item 2</li>\n</ol>\n<p>HTML</p>\n<p class=\"html-example\">This text is wrapped by a div with an html class</p>",
